// Reset using Normalize
@import "normalize";

// Import fonts from Google
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,800;1,400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=JetBrains+Mono:ital@0;1&display=swap");

// Site configuration
@import "config";

// Import Partials
@import "partials/typography";
@import "partials/mixins";

// Modules
@import "modules/buttons";
@import "modules/tables";
@import "modules/syntax";

// Mobile up base styles
@import "breakpoints/mobileup";

// Import media queries add more as you desire
@media only screen and (min-width: 800px) {
  @import "breakpoints/800up";
}
@media only screen and (min-width: 1200px) {
  @import "breakpoints/1200up";
}

// If IE support is needed you can add them here or create another stylesheet for specific IE fixes

.profilePic {
  img {
    margin: 0 auto 2rem;
    display: block;
    border-radius: 100%;
    max-width: 200px;
  }
}

.blob {
  code span {
    background-color: currentColor;
    border-radius: 999px;
  }
}

.Tag {
  color: white;
  background-color: var(--primary-color-dk);
  padding: 0 10px;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: bold;
  display: inline;
  vertical-align: middle;

  &--outlined {
    background: transparent;
    color: currentColor;
    border: 2px solid currentColor;
  }
}

.align-middle {
  vertical-align: middle;
}

.italic {
  font-style: italic;
}

.font-normal {
  font-weight: normal;
}

.font-xl {
  font-size: 1.5rem;
}

.font-sm {
  font-size: 0.8em;
}

.text-center {
  text-align: center;
}

.block {
  display: block;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.h-auto {
  height: auto;
}

.rounded {
  border-radius: 0.25rem;
}

.max-w-sm {
  max-width: 24rem;
}

.dark-safe {
  filter: var(--dark-safe);
}

.Categories {
  margin-bottom: 2rem;

  h2 {
    font-size: 1.5rem;
    margin-left: 0;
  }
}
