/***************
A MORE MODERN SCALE
http://typecast.com/blog/a-more-modern-scale-for-web-typography
***************/
body .intro {
  font-size: 1.25em;
  line-height: 1.7;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  margin: 0.5rem 0 1.5rem;
  font-family: var(--sans-serif-font);
}

h1, .h1 {
  font-size: 2em;
  line-height: 1.25;
}

@media (min-width: 43.75em) {
  h1, .h1 {
    font-size: 2.5em;
    line-height: 1.125;
  }
}
@media (min-width: 56.25em) {
  h1, .h1 {
    font-size: 2.5em;
    line-height: 1.05;
  }
}
h2, .h2 {
  font-size: 1.625em;
  line-height: 1.15384615;
}

@media (min-width: 43.75em) {
  h2, .h2 {
    font-size: 2em;
    line-height: 1.25;
  }
}
@media (min-width: 56.25em) {
  h2, .h2 {
    font-size: 2em;
    line-height: 1.25;
  }
}
h3, .h3 {
  font-size: 1.375em;
  line-height: 1.13636364;
}

@media (min-width: 43.75em) {
  h3, .h3 {
    font-size: 1.5em;
    line-height: 1.25;
  }
}
@media (min-width: 56.25em) {
  h3, .h3 {
    font-size: 1.75em;
    line-height: 1.25;
  }
}
h4, .h4 {
  font-size: 1.125em;
  line-height: 1.11111111;
}

@media (min-width: 43.75em) {
  h4, .h4 {
    line-height: 1.22222222;
  }
}
blockquote {
  font-size: 1.25em;
  line-height: 1.25;
  padding: 0;
}

@media (min-width: 43.75em) {
  blockquote {
    font-size: 1.25em;
    line-height: 1.45833333;
  }
}
code,
kbd,
samp,
pre {
  font-family: var(--monospace-font);
}

/***************
CLEAR AFTER FLOATS
***************/
.clearfix {
  zoom: 1;
}
.clearfix:before, .clearfix:after {
  content: " ";
  display: block;
  height: 0;
  overflow: hidden;
}
.clearfix:after {
  clear: both;
}

/***************
BUTTON DEFAULTS
***************/
.button--outlined, .button {
  background-color: var(--primary-color);
  display: inline-block;
  position: relative;
  font-family: var(--sans-serif-font);
  text-decoration: none;
  color: white;
  font-size: 1em;
  line-height: 1.2em;
  font-weight: normal;
  padding: 0.5em 1.5em;
  border: 0;
  border-radius: 50px;
  cursor: pointer;
  margin-bottom: 0.5em;
  transition: background-color 0.14s ease-in-out;
}
.button--outlined:hover, .button:hover, .button--outlined:focus, .button:focus {
  color: white;
  text-decoration: none;
  background-color: var(--primary-color-dk);
}
.button--outlined:active, .button:active {
  top: 1px;
}

.button--outlined {
  background-color: transparent;
  border: 2px solid var(--primary-color);
  color: var(--primary-color);
}
.button--outlined:hover {
  background-color: var(--primary-color);
  color: var(--page-bg-color);
}

/***************
TABLE STYLES
***************/
table {
  width: 100%;
  border: 1px solid #d0d0d0;
  margin-bottom: 1.5em;
}
table caption {
  margin: 0 0 7px;
  font-size: 0.75em;
  color: #aaa;
  text-transform: uppercase;
  letter-spacing: 1px;
}

tr {
  border-bottom: 1px solid #d0d0d0;
}
tr:nth-child(even) {
  background-color: #f7f7f7;
}

td {
  padding: 7px;
  border-right: 1px solid #d0d0d0;
}
td:last-child {
  border-right: 0;
}

th {
  background-color: #f7f7f7;
  border-bottom: 1px solid #d0d0d0;
  border-right: 1px solid #d0d0d0;
}
th:last-child {
  border-right: 0;
}

.highlight {
  white-space: pre;
  overflow: auto;
  word-wrap: normal;
  /* horizontal scrolling */
  -moz-border-radius: 5px;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  padding: 20px;
  background: var(--code-background-color);
  color: #c1c2c3;
}

.highlight .hll {
