/***************
GLOBAL STYLES
***************/
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: var(--serif-font);
  line-height: 1.7;
  font-size: 110%;
  color: var(--primary-text-color);
  background-color: var(--page-bg-color);
  overflow-x: hidden;
  margin: 0;
}
body p,
body ul {
  margin: 0 0 2rem;
}

img,
video,
audio,
iframe,
object {
  max-width: 100%;
}

.container {
  max-width: 610px;
  padding: 0 20px;
  margin-left: auto;
  margin-right: auto;
}

/***************
LINK STYLING
***************/
a {
  color: var(--primary-color);
  text-decoration: none;
}
a:hover {
  color: var(--primary-color-dk);
}
a code.language-plaintext.highlighter-rouge {
  text-decoration: underline;
}
a code.language-plaintext.highlighter-rouge:hover {
  color: var(--primary-color-dk);
}

/***************
MENU STYLING
***************/
.nav-collapse {
  z-index: 100;
}
.nav-collapse ul {
  margin: 0;
  padding: 0;
  width: 100%;
  display: block;
  list-style: none;
  background: var(--nav-background-color);
  backdrop-filter: blur(0.5rem);
  height: calc(100vh - 3em);
}
@media only screen and (min-width: 800px) {
  .nav-collapse ul {
    height: unset;
    background: none;
    backdrop-filter: none;
  }
}
.nav-collapse ul li {
  width: 100%;
  display: block;
}
.nav-collapse ul li button.theme-toggle {
  width: 100%;
  text-align: inherit;
  background: none;
  border: none;
  cursor: pointer;
}
.nav-collapse ul li button.theme-toggle:before {
  content: var(--toggle-theme-icon);
}
.nav-collapse ul li button.theme-toggle:after {
  content: var(--toggle-theme-word);
}
.nav-collapse ul li a,
.nav-collapse ul li button {
  color: var(--primary-text-color);
  font-size: 1.5rem;
  font-weight: 100;
  text-decoration: none;
  display: block;
  padding: 1rem 2rem;
}
@media only screen and (min-width: 800px) {
  .nav-collapse ul li a,
.nav-collapse ul li button {
    font-weight: normal;
  }
}

.js .nav-collapse {
  clip: rect(0 0 0 0);
  max-height: 0;
  position: absolute;
  display: block;
  overflow: hidden;
  zoom: 1;
  clear: both;
  width: 100%;
}

.nav-collapse.opened {
  max-height: 9999px;
}

.nav-toggle {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -o-user-select: none;
  user-select: none;
  float: right;
  line-height: 0.9;
  background-color: var(--background-color);
  border-radius: 4px;
  padding: 2px 0;
  color: white;
  text-decoration: none;
  position: relative;
  transition: all 0.2s ease-in-out;
  font-weight: bolder;
  margin-top: 8px;
  font-size: 2.15em;
  font-weight: normal;
  right: 0.5rem;
  width: 2.75rem;
  text-align: center;
}
.nav-toggle:hover {
  color: white;
  background-color: var(--background-color-dk);
}
.nav-toggle:after {
  content: "â‰¡";
}
.nav-toggle.active:after {
  content: "Ã—";
}

/***************
LIST STYLING
***************/
.noList {
  list-style: none;
  padding-left: 0;
  margin-left: 0;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 0 1.5rem;
}

.post ul ul,
.post ul ol,
.post ol ul,
.post ol ol {
  margin: 0;
}

/***************
PAGE STYLES
***************/
.pageTitle {
  margin: 2rem auto 1em;
  text-align: center;
}

.content {
  padding-top: 1em;
}

/***************
HEADER STYLING
***************/
.header {
  background-image: var(--nav-background-image);
  background-size: cover;
  background-position: center center;
  color: white;
  padding: 0;
  height: 3em;
}
.header .container {
  padding: 0;
  max-width: 100%;
}
.header h1 {
  margin: 0;
  float: left;
  padding-left: 2rem;
  font-size: 1em;
  line-height: 3em;
}
.header h1 a {
  color: white;
  text-decoration: none;
}

/***************
POST STYLING
***************/
.post .postTitle {
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 1rem;
}
.post blockquote {
  clear: both;
  margin: 2.5em 0;
  padding: 0;
  line-height: 1.8;
  position: relative;
}
.post blockquote p {
  text-align: center;
}
.post blockquote:before {
  content: "";
  position: absolute;
  top: -1.25em;
  background: var(--background-color);
  display: block;
  height: 2px;
  width: 150px;
  left: calc(50% - 75px);
}
.post blockquote:after {
  content: "";
  position: absolute;
  bottom: -1.25em;
  background: var(--background-color);
  display: block;
  height: 2px;
  width: 150px;
  left: calc(50% - 75px);
}
.post .meta {
  text-align: center;
  font-family: var(--sans-serif-font);
  font-weight: 300;
  color: var(--secondary-text-color);
}
.post figure {
  margin: 3rem 0;
}
.post figure figcaption {
  margin-top: 1rem;
  text-align: center;
  font-size: 0.8em;
  color: var(--secondary-text-color);
}
.post figure img {
  margin: 0 auto;
  display: block;
}
.post img {
  margin-bottom: 2rem;
}
.post .highlight {
  clear: both;
  margin-bottom: 2rem;
}
.post pre.highlight {
  margin: 0;
  padding: 0;
  font-size: 0.8em;
}
@media only screen and (max-width: 800px) {
  .post figure.highlight {
    font-size: 0.8em;
  }
}
.post .dropcap {
  color: var(--primary-color);
  margin-right: 0.45rem;
}
.post .featuredImage {
  position: relative;
  margin-top: 2rem;
}
.post .featuredImage img {
  margin-bottom: 0;
}

/***************
POST NAV STYLING
***************/
.postNav {
  border-top: 1px solid var(--footer-border-color);
  border-bottom: 1px solid var(--footer-border-color);
  overflow: hidden;
}
.postNav .prev,
.postNav .next {
  display: block;
  width: 100%;
  height: 5rem;
  overflow: hidden;
  position: relative;
  font-family: var(--sans-serif-font);
  font-weight: 800;
  font-size: 0.9em;
  text-transform: uppercase;
  text-align: center;
}
.postNav .prev img,
.postNav .next img {
  z-index: 1;
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 100%;
  height: auto;
}
.postNav .prev span,
.postNav .next span {
  z-index: 10;
  position: relative;
  padding: 0.5rem;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
  line-height: 1.4;
}
.postNav .prev.image,
.postNav .next.image {
  text-decoration: none;
  color: white;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
  padding: 0 0.5rem;
  position: relative;
  transition: all 0.2s ease-in-out;
}
.postNav .prev:hover.image,
.postNav .next:hover.image {
  opacity: 0.8;
}
.postNav .prev span {
  left: 0;
}
.postNav .next span {
  right: 0;
}

/***************
FOOTER STYLING
***************/
.footer {
  text-align: center;
  padding: 2rem 0;
  clear: both;
}
.footer .container {
  border-top: 4px solid var(--footer-border-color);
  padding-top: 0.5em;
  max-width: 90%;
}
.footer .copy,
.footer .footer-links {
  font-size: 0.8em;
}
.footer .copy {
  margin-bottom: 1rem;
}
.footer-links ul {
  text-align: center;
}
.footer-links ul li {
  display: inline-block;
  margin-right: 8px;
}
.footer-links ul li:last-child {
  margin-right: 0;
}
.footer-links ul li a {
  display: inline-block;
  width: 30px;
  height: 30px;
  transition: all 0.2s ease-in-out;
}
.footer-links ul li a svg {
  display: block;
  width: 100%;
  height: 100%;
}
.footer-links ul li a svg .outer-shape {
  fill: var(--primary-color);
}
.footer-links ul li a svg .inner-shape {
  fill: var(--page-bg-color);
}
.footer-links ul li:hover a svg .outer-shape {
  fill: var(--primary-color-dk);
}

/***************
PAGINATION STYLING
***************/
.pagination .next {
  float: left;
}
.pagination .previous {
  float: right;
}

/***************
POST LIST STYLING
***************/
.posts li {
  margin-bottom: 4rem;
}
.posts li .date {
  color: var(--secondary-text-color);
  font-family: var(--sans-serif-font);
  font-weight: 300;
  font-size: 0.8em;
}
.posts li h3 {
  margin: 0 0 0.5em;
}

.embedded-video {
  display: flex;
  justify-content: center;
}
.embedded-video iframe {
  width: 100%;
  height: auto;
  aspect-ratio: 16/9;
  max-width: 800px;
}

code.language-plaintext.highlighter-rouge {
  background-color: var(--inline-code-background-color);
  color: var(--inline-code-color);
  padding: 1px 8px;
  border-radius: 8px;
}

.Note {
  --inline-code-background-color: #737d8c3a;
  --inline-code-color: currentColor;
  position: relative;
  padding: 1.25rem 2rem;
  border-radius: 0.25rem;
  border-left: 4px solid;
  font-size: 1rem;
}
.Note .title {
  margin-bottom: 1rem;
  display: block;
}
.Note p.content {
  margin: 0;
  padding: 0;
}
.Note a {
  text-decoration: underline;
}
.Note .icon-box {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  border-radius: 9999px;
  background-color: var(--page-bg-color);
  font-size: 15px;
  transform: translate(calc(-50% - 1.5px), -50%);
}
.Note .icon-box .icon {
  display: block;
  font-weight: bold;
  text-align: center;
  width: 1.5rem;
  height: 1.5rem;
  line-height: 1.5rem;
  border-radius: 9999px;
}
.Note--info {
  background: var(--bg-info-color);
  border-left-color: var(--info-color);
}
.Note--info .icon {
  color: var(--info-color);
  box-shadow: 0 0 0 3px var(--info-color);
}
.Note--warn {
  background: var(--bg-warn-color);
  border-left-color: var(--warn-color);
}
.Note--warn .icon {
  color: var(--warn-color);
  box-shadow: 0 0 0 3px var(--warn-color);
}
.Note--error {
  background: var(--bg-error-color);
  border-left-color: var(--error-color);
}
.Note--error .icon {
  color: var(--error-color);
  box-shadow: 0 0 0 3px var(--error-color);
}

kbd {
  font-size: 1rem;
  padding: 3px 5px;
  display: inline-block;
  line-height: 1rem;
  vertical-align: middle;
  background-color: var(--page-bg-color);
  background-color: #00000005;
  border: solid 1px var(--inline-code-background-color);
  border-bottom-color: var(--inline-code-background-color);
  border-radius: 6px;
  box-shadow: inset 0 -1px 0 var(--inline-code-background-color);
}

.logo {
  font-family: var(--monospace-font);
}
.logo .shell {
  color: var(--blue-lt);
}
.logo .dir {
  color: var(--green);
}
.logo .text {
  display: inline-flex;
  line-height: 1em;
  border-right: 0.5rem solid transparent;
  overflow: hidden;
  white-space: nowrap;
  box-shadow: 0.1em 0 0 0 var(--highlight-color);
  --blink-cursor-animation: blink-cursor 1.25s step-end 2s infinite;
  animation: typing-small 1.75s steps(9, end), var(--blink-cursor-animation);
}
@media only screen and (min-width: 800px) {
  .logo .text {
    animation: typing-large 1.75s steps(9, end), var(--blink-cursor-animation);
  }
}
@keyframes typing-small {
  from {
    width: 14%;
  }
  to {
    width: 84%;
  }
}
@keyframes typing-large {
  from {
    width: 11%;
  }
  to {
    width: 84%;
  }
}
@keyframes blink-cursor {
  from, to {
    box-shadow: none;
  }
  50% {
    box-shadow: 0.1em 0 0 0 var(--highlight-color);
  }
}

@media only screen and (min-width: 800px) {
  /***************
  GLOBAL STYLES
  ***************/
  body {
    font-size: 120%;
  }

  .content {
    padding-top: 0;
    position: relative;
    z-index: 1;
  }

  .container {
    max-width: 880px;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
  }

  /***************
  HOMEPAGE STYLING
  ***************/
  #home .posts,
#home .pageTitle,
#home .pagination {
    width: 90%;
    float: none;
    margin: 0 auto 1.5em;
  }
  #home .pageTitle {
    margin: 2rem auto 1em;
  }

  /***************
  HEADER STYLING
  ***************/
  .header {
    height: 8em;
    position: relative;
  }
  .header .container {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    max-width: 880px;
    overflow: hidden;
  }
  .header h1 {
    font-size: 3em;
    line-height: 1;
    letter-spacing: -3px;
    padding-left: 0;
  }
  .header nav {
    float: left;
    clear: left;
  }
  .header nav ul {
    margin: 0;
  }
  .header nav ul li {
    display: inline;
    line-height: 3em;
    background: none;
    padding: 0 5px;
    border: none;
  }
  .header nav ul li a, .header nav ul li button {
    color: white;
    font-size: 0.9em;
    text-decoration: none;
    padding: 0 0.25em;
  }
  .header nav ul li a:hover, .header nav ul li button:hover {
    color: var(--highlight-color);
  }
  .header nav ul li .theme-toggle {
    width: fit-content;
  }
  .header nav ul li.current {
    padding-bottom: 0.25em;
    border-bottom: 4px solid var(--highlight-color);
  }

  /***************
  MENU STYLING
  ***************/
  .js .nav-collapse {
    position: relative;
    max-height: none;
  }
  .js .nav-collapse.closed {
    max-height: none;
  }
  .js .nav-collapse ul li a, .js .nav-collapse button {
    display: inline-block;
  }

  .nav-toggle {
    display: none;
  }

  /***************
  POST STYLING
  ***************/
  .post h1,
.post h2,
.post h3,
.post h4,
.post h5,
.post h6,
.post p,
.post ul,
.post ol,
.post dl,
.post aside.Note,
.post .pageTitle {
    width: 90%;
    float: none;
    margin: 0 auto 2rem;
  }
  .post h1,
.post .h1,
.post h2,
.post .h2,
.post h3,
.post .h3,
.post h4,
.post .h4,
.post h5,
.post .h5,
.post h6,
.post .h6 {
    margin: 1rem auto;
  }
  .post p {
    text-align: justify;
  }
  .post .pageTitle {
    margin: 2rem auto 1em;
  }
  .post .featuredImage {
    position: relative;
    margin-top: 2rem;
  }
  .post .featuredImage:before {
    background: white;
    content: "";
    display: block;
    width: 100%;
    height: 4em;
    position: absolute;
    top: -2em;
    left: 0;
    transform: skewY(-2deg);
    transform-origin: bottom left;
  }

  .js .nav-collapse {
    clip: rect(0 0 0 0);
    max-height: 0;
    position: absolute;
    display: block;
    overflow: hidden;
    zoom: 1;
    clear: both;
    width: 100%;
  }

  /***************
  POST NAV STYLING
  ***************/
  .postNav .prev,
.postNav .next {
    width: 50%;
  }
  .postNav .prev {
    float: left;
    text-align: left;
  }
  .postNav .next {
    text-align: right;
    float: right;
  }

  /***************
  FOOTER STYLES
  ***************/
  .footer-links {
    float: right;
  }
  .footer .copy {
    float: left;
  }
  .footer .container {
    padding: 1rem 0;
    max-width: 880px;
  }

  .post figure.highlight > pre,
.post pre.highlight {
    margin: 0;
    padding: 0;
    font-size: 0.7em;
  }
}
@media only screen and (min-width: 1200px) {
  .container,
.header .container,
.footer .container {
    max-width: 1000px;
  }

  .header {
    height: 8em;
  }
}
.profilePic img {
  margin: 0 auto 2rem;
  display: block;
  border-radius: 100%;
  max-width: 200px;
}

.blob code span {
  background-color: currentColor;
  border-radius: 999px;
}

.Tag {
  color: white;
  background-color: var(--primary-color-dk);
  padding: 0 10px;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: bold;
  display: inline;
  vertical-align: middle;
}
.Tag--outlined {
  background: transparent;
  color: currentColor;
  border: 2px solid currentColor;
}

.align-middle {
  vertical-align: middle;
}

.italic {
  font-style: italic;
}

.font-normal {
  font-weight: normal;
}

.font-xl {
  font-size: 1.5rem;
}

.font-sm {
  font-size: 0.8em;
}

.text-center {
  text-align: center;
}

.block {
  display: block;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.h-auto {
  height: auto;
}

.rounded {
  border-radius: 0.25rem;
}

.max-w-sm {
  max-width: 24rem;
}

.dark-safe {
  filter: var(--dark-safe);
}

.Categories {
  margin-bottom: 2rem;
}
.Categories h2 {
  font-size: 1.5rem;
  margin-left: 0;
}